.tovchnuud {
  display: flex;
  background-color: azure;
  justify-content: center;
  padding: 12px;
  gap: 10px;
  border-radius: 20px;
}
.tovchnuud button {
  background: linear-gradient(to top, #33ccff 0%, #ff33cc 100%);
  color: white;
  padding: 12px;
  border-radius: 12px;
  box-shadow: 10px 15px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 10px 15px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 15px 5px 0px rgba(0, 0, 0, 0.75);
  transition: 0.3;
}

